body, h1, h2, h3, h4, p,
figure, blockquote, dl, dd {
  margin: 0;
}

:root {
  --violet: #9900FF;
  --magenta: #FF33FF;
  --yellow: #F9F906;
  --green: #99FF00;
  --blue: #0000FF;
  --black: #000000;
  --darkgray: rgb(77, 77, 77);
  --dove-gray: #666666;
  --main-bg: linear-gradient(to right, #FFC72C, #E56A54, #E10098, #84329B, #24135F);
}

