  padding: 0;
  font-family: inherit;
  background-color: transparent;
  border: none;
}

a {
  color: inherit;
  text-decoration: none;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
