.about{
    margin-top: 40px;
    font-size: 18px;

    @media (min-width: 650px) {
        margin-top: 100px;
    }
}

.quotation {
    background-color: #fff;

    &__inner {
        position: relative;

        margin-top: 30px;
        padding: 20px;

        background-color: #fff;
        box-shadow: 0px 7px 8px 0px rgba(34, 60, 80, 0.2);

        @media (min-width: 650px) {
            width: 65%;
            margin-top: -30px;
            margin-left: -5%;
            padding: 10px;
            padding-right: 40px;
            padding-left: 30px;
        }
        
        @media (min-width: 998px) {
            width: 65%;
            margin-top: -30px;
            margin-left: -5%;
            padding: 40px;
            padding-right: 120px;
            padding-left: 90px;
        }
    }

    &__image {
        width: 40%;

        img {
            aspect-ratio: 12/15;
            width: 100%;
            object-fit: cover;
            filter: grayscale(1);
        }
    }

    &__brace {
        position: absolute;
        top: 8px;
        left: 7px;

        width: 18px;
        height: 10px;

        @media (min-width: 998px) {
            top: 35px;
            left: 37px;
            width: 28px;
            height: 20px;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
    }

    &__column {
        display: flex;
        flex-direction: column;
    }

    @media (min-width: 650px) {
        display: flex;
        align-items: start;
    }
}

.about-history{
    font-size: 18px;
}
