.footer {
    margin-top: 40px;
    padding-top: 30px;
    color: white;
    background: var(--main-bg);

    &__top {
        padding-bottom: 40px;

        @media (min-width: 700px) {
            display: flex;
            gap: 40px;
            align-items: center;
        }
    }

    &__info {
        text-align: center;
    }

    &__logo {
        margin-bottom: 20px;

        img {
            width: 100px;
            filter: brightness(0) invert(1);

            @media (min-width: 998px) {
                width: 150px;
            }
        }
    }

    &__nav {
        @media (min-width: 700px) {
            display: flex;
            gap: 40px;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        margin-bottom: 7px;

        .font-icon{
            width: 20px;
        }
    }

    &__link {
        transition: color 0.3s;

        &:hover {
            color: var(--hover-color);
        }
    }
}

.copyright {
    display: flex;
    align-items: center;
    justify-content: center;

    padding: 10px 0;

    font-size: 14px;
    color: var(--darkgray);

    background-color: #fff;
}
