body {
    margin: 0;
    padding: 0;

    font-family: EBGaramond, sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: var(--darkgray);

    background-color: white;
}

html,
*,
*::after,
*::before {
    box-sizing: border-box;
}

html{
    scroll-behavior: smooth;
}

img {
    max-width: 100%;
    margin: 0;
    padding: 0;
    vertical-align: middle;
}

button {
    cursor: pointer;

    margin: 0;
    padding: 0;

    font-family: inherit;

    background-color: transparent;
    border: none;
}

a {
    color: inherit;
    text-decoration: none;
}

ul,
ol,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-size: inherit;
    font-weight: inherit;
}

.container {
    margin: 0 auto;
    padding: 0 20px;

    @media (min-width: 1024px) {
        max-width: 946px;
    }

    @media (min-width: 1300px) {
        max-width: 1220px;
    }

    @media (min-width: 1500px) {
        max-width: 1420px;
    }

    @media (min-width: 1600px) {
        max-width: 1520px;
    }

    @media (min-width: 1800px) {
        max-width: 1440px;
    }
}

.container2 {
    max-width: 1340px;
    margin: 0 auto;
    padding: 0 20px;

   
}


.noscroll {
    overflow: hidden;
}

.font-icon {
    font-family: FontAwesome, sans-serif;

}


.title-big {
    margin-top: 40px;
    margin-bottom: 20px;
    font-size: 28px;
    font-weight: 700;

    @media (min-width: 640px) {
        font-size: 30px;
    }

    @media (min-width: 1024px) {
        font-size: 32px;
    }
}

.title {
    margin-top: 30px;
    margin-bottom: 20px;

    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;

    @media (min-width: 1024px) {
        margin-top: 60px;
        font-size: 26px;
    }
}

.subtitle {
    margin-top: 20px;
    margin-bottom: 20px;

    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;


    @media (min-width: 1024px) {
        margin-top: 40px;
        font-size: 22px;
    }
}

.text-banner {
    font-size: 18px;
    font-weight: 600;

    @media (min-width: 520px) {
        font-size: 22px;
    }

    @media (min-width: 1024px) {
        font-size: 28px;
    }
}

.subtitle-sm {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 500;

    @media (min-width: 1024px) {
        font-size: 20px;
    }
}

.center{
    text-align: center;
}

.left{
    text-align: left;
}

.text {
    font-size: 18px;
    font-weight: 400;

    @media (min-width: 1024px) {
        font-size: 20px;
    }
}


.text-600 {
    font-size: 18px;
    font-weight: 600;

    @media (min-width: 1024px) {
        font-size: 18px;
    }
}


.text-big {
    font-size: 18px;
    font-weight: 500;

    @media (min-width: 1024px) {
        font-size: 22px;
    }
}

.text-big-700 {
    font-size: 18px;
    font-weight: 700;

    @media (min-width: 1024px) {
        font-size: 26px;
    }
}

.text-sm {
    font-size: 14px;
    font-weight: 400;

    @media (min-width: 640px) {
        font-size: 16px;
    }
}

.text-xs {
    font-size: 12px;
    font-weight: 400;

    @media (min-width: 640px) {
        font-size: 14px;
    }
}

.hidden {
    display: none;
}


.burger{
    display: block;

    padding: 5px 10px;
    
    color:rgba(0,0,0,.1);
   
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;

    span{
        display: inline-block;

        width: 30px;
        height: 30px;

        vertical-align: middle;

        opacity: 0.5;
        background: no-repeat center center;
        background-image: url(../images/burger.svg);
        background-size: 100% 100%;
    
    }

    @media (min-width: 950px) {
        display: none;;
    }

}

.close-navbar{
    position: absolute;
    top: 30px;
    right: 30px;
  
    font-size: 30px;
    font-weight: 700;
    color: white;
    text-shadow: 0 1px 0 #fff;

    @media (min-width: 950px) {
        display: none;;
    }
}

.breadcrumbs-nav{
    margin: 20px 0;
}

.breadcrumbs{
    display: flex;
    align-items: center;
    font-size: 18px;
    color: var(--darkgray);

    a{
        color: var(--violet);
    }

    &__item + &__item::before{
        content: "/";
        padding:0 5px;
    }
}

