.header {
    position: sticky;
    z-index: 10;
    top: 0;
    left: 0;

    box-shadow: 0px 7px 8px 0px rgba(34, 60, 80, 0.2);

    &__top {
        padding: 10px 0;
        color: #fff;
        background: var(--main-bg);

        .container2 {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    &__bottom {
        padding: 10px 0;
        background-color: #fff;

        .container2 {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    &__logo {
        display: flex;
        gap: 20px;
        align-items: center;
    }

    &__logo2 {
        width: 100px;

        @media (min-width: 998px) {
            width: 150px;
        }
    }

    &__logo-navbar {
        width: 170px;

        img {
            filter: brightness(0) invert(1);
        }

        @media (min-width: 950px) {
            display: none;
        }
    }

    &__logo1 {
        width: 60px;

        @media (min-width: 998px) {
            width: 100px;
        }
    }

    &__menu {
        position: relative;
        display: flex;
        gap: 30px;
        align-items: center;


        @media (max-width: 950px) {
            position: fixed;
            z-index: 1;
            inset: 0;
            transform: translateY(-100%);

            overflow-y: auto;
            flex-direction: column;
            gap: 15px;

            padding-top: 80px;

            color: #fff;

            opacity: 0;
            background: linear-gradient(
                to right,
                rgba(229, 106, 84, 0.95),
                rgba(225, 0, 152, 0.95),
                rgba(132, 50, 155, 0.95)
            );

            transition: 0.5s;

            &.open {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }
}

.menu {
    text-transform: uppercase;

    &__link {
        display: block;
        padding: 10px 0;
        text-transform: uppercase;
        text-wrap: nowrap;

        &:hover {
            color: var(--magenta);
        }

        &--toggle {
            position: relative;

            &::after {
                content: "";
                
                position: absolute;
                top: 18px;
                right: -14px;

                display: inline-block;
                
                margin-left: 0.255em;
                
                vertical-align: 0.255em;
                
                border-top: 0.3em solid;
                border-right: 0.3em solid transparent;
                border-bottom: 0;
                border-left: 0.3em solid transparent;
            }
        }

        &--mobile {
            @media (min-width: 950px) {
                display: none;
            }
        }

        &--desktop {
            @media (max-width: 950px) {
                display: none;
            }
        }
    }

    &__submenu {
        position: absolute;
        top: 100%;
        left: 0;

        display: flex;
        flex-direction: column;
        align-items: start;

        padding: 10px 20px 10px 10px;

        font-size: 14px;

        visibility: hidden;
        opacity: 0;
        background-color: #fff;
        box-shadow: 2px 2px 8px 0px rgba(34, 60, 80, 0.2);

        @media (max-width: 950px) {
            overflow: hidden;
            position: static;

            align-items: center;

            max-height: 0;
            padding: 0 90px;

            
            color: var(--darkgray);
            
            visibility: visible;
            opacity: 1;
            box-shadow: none;

            transition:  max-height 0.3s;
           
            .submenu-open &{
                max-height:400px;
                padding: 10px 90px;
            }
         
        }
        
    }

    &__item {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        @media (min-width: 950px) {
            margin-right: 10px;
        }
    }

    &__item:hover &__submenu {
        @media (min-width: 950px) {
            visibility: visible;
            opacity: 1;
            border-top: 2px solid var(--magenta);
        }
    }
}
