.pet {
    &__about{
        font-size: 18px;
    }

    &__foto {
        display: flex;

        img {
            aspect-ratio: 3/2;
            width: 50%;
        }
    }

    &__title {
        text-transform: uppercase;
    }

    &__block {
        color: var(--darkgray);

        .container {
            display: flex;
            flex-direction: column;
            gap: 20px;
            align-items: center;

            @media (min-width: 900px) {
                flex-direction: row;
                align-items: start;
            }
        }
    }

    &__image {
        padding: 10px;
        box-shadow: 0px 7px 8px 0px rgba(34, 60, 80, 0.2);


        img {
            aspect-ratio: 1/1;
            width: 100%;
            object-fit: contain;
        }

        @media (min-width: 600px) {
            width: 40%;
        }


    }

    &__text {
        @media (min-width: 900px) {
            width: 55%;
        }
    }
}
