.main-slider {
    &__slider {
        position: relative;
    }

    &__prev {
        left: 30px;
    }

    &__next {
        right: 30px;
    }

    &__prev,
    &__next {
        position: absolute;
        z-index: 1;
        top: 50%;
        transform: translateY(-50%);

        display: none;

        width: 34px;
        height: 34px;

        color: var(--magenta);

        opacity: 0.7;
        background-color: white;
        border: 1px solid var(--magenta);
        border-radius: 100%;

        transition: 0.3s;

        &:hover {
            opacity: 1;
        }

        @media (min-width: 998px) {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__pagination {
        position: absolute;
        z-index: 1;
        bottom: 20px !important;

        display: flex;
        gap: 5px;
        justify-content: center;
    }

    &__bullet {
        display: block;

        width: 10px;
        height: 10px;

        background-color: white;
        border: 1px solid white;
        border-radius: 50%;

        transition:
            transform 0.2s,
            background-color 0.2s;

        @media (min-width: 998px) {
            width: 15px;
            height: 15px;
        }
    }

    &__bullet--active {
        background-color: var(--magenta);
    }

    &__slide {
        aspect-ratio: 3/2;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }

        @media (min-width: 450px) {
            aspect-ratio: 7/3;
        }
    }
}

.main-about {
    p {
        font-size: 18px;
        color: var(--dove-gray);
    }
}

.main-zones {
    margin-top: 40px;
    padding: 40px 0;

    font-size: 18px;
    color: #fff;
    
    background: url(../images/bg-zone.png) center center no-repeat;
    background-size: cover;

    &__title {
        margin-bottom: 30px;
        text-transform: uppercase;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        row-gap: 30px;
    }

    &__block {
        margin-top: 30px;
        text-align: center;

        p {
            margin-bottom: 20px;
        }
    }
}

.card-zone {
    display: flex;
    flex-direction: column;

    width: 50%;
    padding: 0 10px;

    color: white;

    &__image {
        overflow: hidden;
        aspect-ratio: 1/1;
        margin-bottom: 10px;

        img {
            height: 100%;
            object-fit: cover;
            transition: transform 0.3s;
        }
    }

    &__link:hover img {
        transform: scale(1.2);
    }

    &__link:hover p {
        color: var(--green);
    }

    @media (min-width: 650px) {
        width: 33%;
    }

    @media (min-width: 998px) {
        width: 25%;
    }
}

