            height: 100%;
            object-fit: cover;
            transition: transform 0.3s;
        }
    }

    &__link:hover img {
        transform: scale(1.2);
    }

    &__name {
        margin-top: 20px;
        padding-left: 20px;
    }

    &__link2 {
        &:hover {
            color: var(--magenta);
        }
    }

    &__meta {
        padding-left: 20px;

        li {
            margin-bottom: 5px;
        }

        li::before {
            display: inline-block;

            width: 20px;
            margin-right: 10px;

            font-size: 20px;
            color: var(--magenta);
        }
    }
}

