.partners{
    margin-top: 50px;

    &__list{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;

        img{

            padding: 0 10px;
            margin-top: 20px;

            height: 40px;

            @media (min-width: 450px) {
                padding: 0 15px;
                height: 50px;
            }

            @media (min-width: 650px) {
                padding: 0 30px;
                height: 60px;
            }

            @media (min-width: 1024px) {
                padding: 0 30px;
                height: 100px;
            }
        }
    }
}