.contacts{
    color: var(--darkgray);

    p{
        margin-bottom: 30px;
        font-size: 16px;
    }

    h2{
        margin-bottom: 20px;
        font-size: 24px;
        font-weight: 600;
    }

    iframe{
        max-width: 100%;
    }

    a{
