.zones {
    font-size: 18px;

    &__title {
        color: var(--blue);
        text-transform: uppercase;
    }

    &__block {
        padding: 40px 0;
        color: #fff;
        background: url(../images/bg-zone.png) center center no-repeat;
        background-size: cover;
    }
}

.zones-slider {
    position: relative;
    margin-bottom: 30px;

    /* stylelint-disable-next-line selector-class-pattern */
    &__slide {
        aspect-ratio: 3 / 2;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @media (min-width: 450px) {
            aspect-ratio: 7 / 3;
        }
    }

    &__prev {
        left: 30px;
    }

    &__next {
        right: 30px;
    }

    &__prev,
    &__next {
        position: absolute;
        z-index: 1;
        top: 50%;
        transform: translateY(-50%);

        display: flex;
        align-items: center;
        justify-content: center;

        width: 34px;
        height: 34px;

        color: white;

        opacity: 0.7;
         background-color: var(--blue);
        border-radius: 100%;

        transition: 0.3s;

        &:hover {
            opacity: 1;
        }

    }
}


.zones-map{
    margin-top: 20px;
    text-align: center;

    iframe{
        max-width: 100%;
    }
}